body {
  font-family: 'Open Sans', sans-serif;
  font-family: 'Lato', sans-serif;
}

h1 {
  text-shadow: 3px 2px 3px #333;
}

h3 {
  text-align: center;
}

footer {
  color: white;
  text-align: center;
  height: 150px;
  padding: 50px;
  background-color: transparent !important;
  background-image: url("images/blade_runner___the_eye_by_elclon-d5tb0mm.png");
  background-attachment: fixed;
  background-position: bottom;
  background-size: cover;
}

.jumbotron {
  font-size:55px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing:20px;
  color: white;
  margin: 0px;
  padding-top: 100px;
  background-color: transparent;
  background-image: url("images/blade_runner___the_eye_by_elclon-d5tb0mm.png");
  height: 500px; 
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 92%, 0 100%, 0 calc(100% - 6vw));
  clip-path: polygon(0 0, 100% 0, 100% 92%, 0 100%, 0 calc(100% - 6vw));
}

.main {
  width: 80%;
  margin: auto;
  margin-bottom: 15px;
  padding-bottom: 15px;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.portrait {
  float: left;
  margin: 15px;
  margin-top: 5px;
  width: 50%;
  height: auto;
}

.middle {
  background-image: url("images/RINGER_PKD_FINAL.jpg");
  height: 250px; 
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  -webkit-clip-path: polygon(0 0, 100% 12%, 100% 100%, 0 88%, 0 calc(100% - 6vw));
  clip-path: polygon(0 0, 100% 12%, 100% 100%, 0 88%, 0 calc(100% - 6vw));
}

.lower {
  background-image: url("images/lower.jpg");
  height: 250px; 
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  -webkit-clip-path: polygon(0 12%, 100% 0, 100% 88%, 0 100%, 0 calc(100% - 6vw));
  clip-path: polygon(0 16%, 100% 0, 100% 84%, 0 100%, 0 calc(100% - 6vw));
}

.thumbnail {
  text-align: center;
  border: 0px;
  width: 130px;
  height: 210px;
  margin: 15px;
}

.popover {
  min-width: 400px;
  max-width: 600px;
}